.footer {
  width: 100%;
  background-color: #ffffff;
  padding: 80px 20px 60px 20px;
  position: relative;
}

.footer-container {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer-contact-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
  width: 100%;
}

.footer-contact-text {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #333333;
  margin: 0 0 16px 0;
  text-align: center;
}

.footer-contact-buttons {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: center;
}

.footer-contact-button {
  display: inline-flex;
  align-items: center;
  gap: 10px;
  padding: 12px 24px;
  background-color: #f5f5f5;
  border-radius: 8px;
  text-decoration: none;
  transition: background-color 0.2s ease, transform 0.2s ease;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  font-size: 16px;
  font-weight: 500;
  color: #333333;
}

.footer-contact-button:hover {
  background-color: #e8e8e8;
  transform: translateY(-2px);
}

.footer-contact-icon {
  width: 20px;
  height: 20px;
  color: #333333;
  flex-shrink: 0;
}

.footer-divider {
  width: 100%;
  max-width: 900px;
  height: 1px;
  background-color: #111010;
  margin-bottom: 40px;
}

.footer-content {
  width: 100%;
  max-width: 1000px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 40px;
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, rgba(235, 245, 255, 0.3) 20%, rgba(215, 235, 255, 0.5) 40%, rgba(190, 225, 255, 0.7) 60%, rgba(140, 200, 255, 0.85) 80%, rgba(30, 107, 255, 0.95) 100%);
  padding: 40px;
  border-radius: 16px;
  box-shadow: 0 0 100px rgba(30, 107, 255, 0.15), 0 0 150px rgba(0, 102, 255, 0.1);
}

.footer-links-left,
.footer-links-right {
  display: flex;
  flex-direction: column;
  gap: 16px;
  align-items: flex-start;
  flex: 0 0 auto;
}

.footer-link {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #0c0c0c;
  text-decoration: none;
  transition: color 0.2s ease;
  white-space: nowrap;
}

.footer-link:hover {
  color: #1E6BFF;
  text-shadow: 0 0 8px rgba(30, 107, 255, 0.3);
}

.footer-right-section {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 20px;
  flex: 0 0 auto;
  margin-left: auto;
}

.footer-copyright {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: #0c0c0c;
  margin: 0;
  text-align: right;
  line-height: 1.5;
  max-width: 400px;
}

.footer-social-icons {
  display: flex;
  gap: 12px;
  align-items: center;
}

.footer-social-icon {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #0c0c0c;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  transition: background-color 0.2s ease, transform 0.2s ease;
  flex-shrink: 0;
}

.footer-social-icon:hover {
  background-color: #00B564;
  transform: translateY(-2px);
}

.footer-social-icon svg {
  width: 20px;
  height: 20px;
}

/* Responsive adjustments for footer */
@media (max-width: 968px) {
  .footer-content {
    flex-direction: column;
    align-items: center;
    gap: 40px;
    text-align: center;
  }

  .footer-links-left,
  .footer-links-right {
    align-items: center;
  }

  .footer-right-section {
    align-items: center;
    margin: 0;
  }

  .footer-copyright {
    text-align: center;
  }
}

@media (max-width: 768px) {
  .footer {
    padding: 60px 20px 40px 20px;
  }

  .footer-content {
    gap: 30px;
  }

  .footer-links-left,
  .footer-links-right {
    gap: 12px;
  }

  .footer-link {
    font-size: 14px;
  }

  .footer-copyright {
    font-size: 14px;
  }

  .footer-social-icon {
    width: 36px;
    height: 36px;
  }

  .footer-social-icon svg {
    width: 18px;
    height: 18px;
  }
}