.hero-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* padding: 40px 0 80px; */
  min-height: calc(100vh - 120px);
  position: relative;
  overflow: hidden;
  background-color: #ffffff;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  z-index: 1;
}

/* Hero Main Image */
.hero-main-image {
  width: 100%;
  max-width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 2;
  animation: fadeInUp 0.8s ease-out;
  opacity: 0;
  animation-fill-mode: forwards;
}

.hero-main-image img {
  margin-top: 50px;
  width: 100%;
  max-width: 100%;
  height: auto;
  object-fit: cover;
  display: block;
}

.hero-click-overlay {
  position: absolute;
  top: 3%;
  left: 50%;
  transform: translateX(-50%);
  width: 45%;
  height: 15%;
  z-index: 10;
  background-color: transparent;
  cursor: pointer;
}

@media (max-width: 768px) {
  .hero-main-image img {
    object-fit: contain;
  }
}

.hero-wrapper {
  width: 100%;
  max-width: 100%;
  display: grid;
  grid-template-columns: auto 1fr auto;
  align-items: start;
  justify-items: center;
  position: relative;
  /* gap: 20px;
  padding: 60px 0 40px 0; */
  background-color: transparent;
}

.hero-wrapper::after {
  content: '';
  position: absolute;
  bottom: -280px;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  max-width: 1200px;
  height: 860px;
  background: linear-gradient(to top, #1E6BFF 0%, rgba(30, 107, 255, 0.98) 6%, rgba(30, 107, 255, 0.92) 12%, rgba(30, 107, 255, 0.82) 18%, rgba(30, 107, 255, 0.68) 24%, rgba(30, 107, 255, 0.50) 30%, rgba(30, 107, 255, 0.32) 36%, rgba(30, 107, 255, 0.18) 42%, rgba(30, 107, 255, 0.08) 48%, rgba(30, 107, 255, 0.02) 54%, transparent 60%, transparent 100%);
  border-radius: 50% 50% 0 0 / 30% 30% 0 0;
  z-index: 0;
  pointer-events: none;
}

.hero-image-left,
.hero-image-right {
  flex-shrink: 0;
  position: relative;
  z-index: 1;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  overflow: visible;
  height: 600px;
  width: auto;
  align-self: start;
  margin-top: 0;
  margin-top: 30px;
}

.hero-image-left {
  justify-self: start;
  align-self: start;
  margin-left: -75px;
  padding-left: 0;
  animation: slideInFromLeft 0.8s ease-out;
  opacity: 0;
  animation-fill-mode: forwards;
}

.hero-image-right {
  justify-self: end;
  align-self: start;
  margin-right: -75px;
  padding-right: 0;
  justify-content: flex-end;
  animation: slideInFromRight 0.8s ease-out;
  opacity: 0;
  animation-fill-mode: forwards;
}

.hero-image-right img {
  width: auto;
  height: 800px;
  max-width: none;
  object-fit: cover;
  object-position: top center;
  display: block;
  transform: scale(1.1);
  transform-origin: top center;
  border-radius: 0 0 150px 0;
}

.hero-image-left img {
  width: auto;
  height: 800px;
  max-width: none;
  object-fit: cover;
  object-position: top center;
  display: block;
  transform: scale(1.1);
  transform-origin: top center;
  border-radius: 0 0 0 150px;
}

.hero-content {
  max-width: 900px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  z-index: 2;
  justify-self: center;
}

.hero-text-container {
  margin-bottom: 40px;
  width: 100%;
  animation: fadeInUp 0.8s ease-out 0.2s;
  opacity: 0;
  animation-fill-mode: forwards;
}

.hero-heading {
  font-family: "Lexend", "Lexend Placeholder", sans-serif;
  font-size: 58px;
  font-weight: 700;
  line-height: 120%;
  color: #0c0c0c;
  margin: 0 0 40px 0;
  padding: 0;
  letter-spacing: -0.05em;
  text-align: center;
  text-decoration: none;
  text-transform: none;
}

.hero-paragraph-container {
  margin-bottom: 10px;
  animation: fadeInUp 0.8s ease-out 0.4s;
  opacity: 0;
  animation-fill-mode: forwards;
}

.hero-paragraph {
  font-family: "Lexend", "Lexend Placeholder", sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: 150%;
  color: #666666;
  text-align: center;
  margin: 0;
  padding: 0;
}

.city-name {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  font-weight: 600;
  font-size: 18px;
  color: #007BFF;
}

.app-buttons-container {
  display: flex;
  gap: 50px;
  justify-content: center;
  align-items: center;
  margin-bottom: 40px;
  flex-wrap: wrap;
  animation: fadeInUp 0.8s ease-out 0.6s;
  opacity: 0;
  animation-fill-mode: forwards;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 20px;
  border-radius: 16px;

}

.app-button {
  display: inline-block;
  height: 70px;
  transition: transform 0.2s ease, opacity 0.2s ease;
  text-decoration: none;
}

.app-button:hover {
  transform: translateY(-2px);
  opacity: 0.9;
}

.app-button img {
  height: 100%;
  width: auto;
  display: block;
  border-radius: 8px;
}

.google-play {
  width: 200px;
}

.app-store {
  width: 200px;
}

.hero-tagline {
  margin-top: 20px;
  animation: fadeInUp 0.8s ease-out 0.8s;
  opacity: 0;
  animation-fill-mode: forwards;
}

.hero-tagline p {
  font-family: "Lexend", "Lexend Placeholder", sans-serif;
  font-size: 19px;
  font-weight: 500;
  line-height: 150%;
  color: #666666;
  text-align: center;
  margin: 0;
  padding: 0;
}

@media (max-width: 1400px) {

  .hero-image-left img,
  .hero-image-right img {
    height: 750px;
    transform: scale(1.05);
  }
}

@media (max-width: 1200px) {

  .hero-image-left img,
  .hero-image-right img {
    height: 700px;
    transform: scale(1.0);
  }

  .hero-wrapper {
    gap: 15px;
  }
}

@media (max-width: 1024px) {

  .hero-image-left img,
  .hero-image-right img {
    height: 650px;
    transform: scale(0.95);
  }
}

@media (max-width: 968px) {
  .hero-wrapper {
    grid-template-columns: 1fr;
    gap: 0;
  }

  .hero-image-left,
  .hero-image-right {
    display: none;
  }
}

@media (max-width: 768px) {
  .hero-container {
    padding-top: 100px;
  }

  .hero-heading {
    font-size: 42px;
  }

  .hero-paragraph {
    font-size: 16px;
  }

  .app-buttons-container {
    flex-direction: column;
    gap: 16px;
  }

  .app-button {
    width: 160px;
    height: 50px;
  }

  .hero-tagline p {
    font-size: 17px;
  }
}

.hero-mobile-section {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -200px;
  padding: 0;
  position: relative;
  z-index: 3;
  animation: fadeInUp 0.8s ease-out 1s;
  opacity: 0;
  animation-fill-mode: forwards;
  /* background-color: #ffffff; */
}

.hero-mobile-image {
  width: 100%;
  max-width: 700px;
  height: auto;
  object-fit: contain;
  object-position: center;
  display: block;
  margin: 0 auto;
}

@keyframes slideInFromLeft {
  0% {
    transform: translateX(-100px);
    opacity: 0;
  }

  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slideInFromRight {
  0% {
    transform: translateX(100px);
    opacity: 0;
  }

  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes fadeInUp {
  0% {
    transform: translateY(30px);
    opacity: 0;
  }

  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@media (max-width: 480px) {
  .hero-heading {
    font-size: 32px;
  }

  .hero-paragraph {
    font-size: 14px;
  }

  .app-button {
    width: 140px;
    height: 45px;
  }

  .hero-mobile-section {
    margin-top: 60px;
  }
}

/* Services Section */
