  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.services-tag {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 8px 20px;
  border: 1px solid #e5e5e5;
  border-radius: 100px;
  background-color: #f8fafc;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  font-size: 14px;
  font-weight: 500;
  color: #0c0c0c;
  margin-bottom: 40px;
}

.services-tag-icon {
  font-size: 16px;
  color: #0c0c0c;
}

.services-heading {
  font-family: 'Lexend', sans-serif;
  font-size: 48px;
  font-weight: 700;
  line-height: 120%;
  letter-spacing: -0.05em;
  color: #0c0c0c;
  margin-bottom: 20px;
  max-width: 800px;
}

.services-subheading {
  font-family: "Lexend", "Lexend Placeholder", sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: 150%;
  color: #0c0c0c;
  margin-bottom: 60px;
  max-width: 600px;
}

.services-marquee-wrapper {
  width: 100vw;
  overflow: hidden;
  position: relative;
  margin-top: 40px;
  margin-left: calc(-50vw + 50%);
  margin-right: calc(-50vw + 50%);
}

.services-marquee {
  display: flex;
  width: fit-content;
  animation: marquee 30s linear infinite;
}

.services-marquee-content {
  display: flex;
  gap: 20px;
  padding: 0;
}

.service-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-shrink: 0;
}

.service-image-card {
  width: 500px;
  height: 400px;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
}

.service-image-card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.service-label {
  margin-top: 16px;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  font-size: 16px;
  font-weight: 600;
  color: #0c0c0c;
  text-align: center;
}

@keyframes marquee {
  0% {
    transform: translateX(0);
  }

  100% {
    transform: translateX(-50%);
  }
}

/* Responsive adjustments for services section */
@media (max-width: 768px) {
  .services-heading {
    font-size: 32px;
  }

  .services-subheading {
    font-size: 16px;
  }

  .service-image-card {
    width: 250px;
    height: 350px;
  }

  .service-label {
    font-size: 14px;
  }
}

