/* How It Works Section */
.how-it-works-section {
  width: 100%;
  background-color: #ffffff;
  padding: 100px 20px;
  position: relative;
}

.how-it-works-container {
  max-width: 1400px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.how-it-works-tag {
  display: inline-flex;
