}

.steps-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
  width: 100%;
  max-width: 1400px;
  margin-top: 40px;
}

.step-card {
  background: linear-gradient(135deg, #1E6BFF 0%, #0066FF 50%, #0099FF 100%);
  border-radius: 24px;
  padding: 40px 30px 50px 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  position: relative;
  overflow: hidden;
  box-shadow: 0 8px 40px rgba(30, 107, 255, 0.4), 0 0 60px rgba(0, 102, 255, 0.3), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  max-height: 600px;
}

.step-badge {
  background-color: rgba(255, 255, 255, 0.2);
  color: #ffffff;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 0.1em;
  padding: 8px 16px;
  border-radius: 8px;
  margin-bottom: 24px;
  text-transform: uppercase;
}

.step-title {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  font-size: 20px;
  font-weight: 700;
  line-height: 140%;
  color: #ffffff;
  margin-bottom: 30px;
  min-height: 56px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.step-phone-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: auto;
}

.step-phone-image {
  width: 100%;
  max-width: 300px;
  height: auto;
  object-fit: contain;
  border-radius: 20px;
  /* box-shadow: 0 8px 30px rgba(0, 0, 0, 0.3); */
  display: block;
}

/* Responsive adjustments for how it works section */
@media (max-width: 1024px) {
  .steps-container {
    grid-template-columns: 1fr;
    gap: 40px;
    max-width: 600px;
  }

  .step-card {
