.navbar-container {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 12px 0 0 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.navbar {
  width: fit-content;
  background-color: rgba(255, 255, 255, 1);
  border-radius: 100px;
  border: 1px solid rgba(221, 229, 237, 1);
  box-shadow: rgba(0, 181, 100, 0.08) 0px 22px 70px 0px;
  backdrop-filter: blur(5px);
  padding: 12px 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: fadeInDown 0.6s ease-out;
  opacity: 0;
  animation-fill-mode: forwards;
}

.navbar-content {
  display: flex;
  align-items: center;
  gap: 8px;
}

.nav-link {
  padding: 8px 16px;
  text-decoration: none;
  color: #333333;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  font-size: 16px;
  font-weight: 500;
  border-radius: 100px;
  transition: background-color 0.2s ease;
  white-space: nowrap;
  line-height: 1.5;
}

.nav-link:hover {
  background-color: rgba(244, 247, 249, 0.5);
}

.nav-logo {
  padding: 0px 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  flex-shrink: 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  font-size: 36px;
  font-weight: 900;
  color: #007BFF;
  white-space: nowrap;
  line-height: 1.2;
  letter-spacing: -0.02em;
}

@keyframes fadeInDown {
  0% {
    transform: translateY(-20px);
    opacity: 0;
  }

  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@media (max-width: 768px) {
  .navbar-content {
    flex-wrap: wrap;
    justify-content: center;
  }

  .navbar {
    padding: 10px 16px;
    width: 95%;
  }

  .nav-link {
    padding: 6px 12px;
    font-size: 14px;
  }

  .nav-logo {
    /* width: 100px; */
    padding: 6px 12px;
  }

}

/* Mobile Sidebar & Toggle Styles */
.nav-links-group {
  display: flex;
  align-items: center;
  gap: 8px;
}

.hamburger-menu {
  display: none;
  flex-direction: column;
  justify-content: space-around;
  width: 24px;
  height: 24px;
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  z-index: 100;
}

.hamburger-menu .bar {
  width: 24px;
  height: 2px;
  background-color: #333333;
  border-radius: 10px;
  transition: all 0.3s linear;
  position: relative;
  transform-origin: 1px;
}

.mobile-sidebar {
  position: fixed;
  top: 0;
  right: -280px;
  width: 280px;
  height: 100vh;
  background-color: #ffffff;
  z-index: 2000;
  box-shadow: -5px 0 15px rgba(0, 0, 0, 0.1);
  transition: right 0.3s ease-in-out;
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.mobile-sidebar.open {
  right: 0;
}

.sidebar-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1500;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease-in-out;
}

.sidebar-overlay.active {
  opacity: 1;
  visibility: visible;
}

.sidebar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
  border-bottom: 1px solid #eee;
  padding-bottom: 15px;
}

.sidebar-logo {
  font-family: 'Inter', sans-serif;
  font-size: 24px;
  font-weight: 900;
  color: #007BFF;
}

.close-sidebar {
  background: none;
  border: none;
  font-size: 32px;
  color: #333;
  cursor: pointer;
  line-height: 1;
}

.sidebar-links {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.sidebar-link {
  font-family: 'Inter', sans-serif;
  font-size: 18px;
  font-weight: 500;
  color: #333;
  text-decoration: none;
  padding: 10px 0;
  border-bottom: 1px solid #f5f5f5;
  transition: color 0.2s;
}

.sidebar-link:hover {
  color: #007BFF;
  padding-left: 5px;
}

/* Hide hamburger on desktop */
@media (min-width: 606px) {
  .mobile-only {
    display: none !important;
  }
}

/* Mobile styles for < 605px */
@media (max-width: 605px) {
  .desktop-only {
    display: none !important;
  }

  .mobile-only {
    display: flex !important;
  }

  .nav-logo {
    /* Ensure logo is visible */
    padding: 0;
  }

  .navbar-content {
    justify-content: space-between;
    width: 100%;
    gap: 0;
  }

  .navbar {
    width: 90%;
    justify-content: space-between;
    padding: 10px 20px;
  }
}